import React from 'react'
import "./Contact.css"
import EmailIcon from '@mui/icons-material/Email';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import PhoneIcon from '@mui/icons-material/Phone';

const Contact = () => {
  return (
    <div className='contact' id='contact'>
      <h4>CONTACT ME</h4>
      <div className='phone' id="contact-phone"><PhoneIcon className='phoneIcon'/> 7038297000</div>
      <div className='contact_div'>
      <div id="contact-email">
        <a href="mailto:asifshaikh0024@gmail.com"><EmailIcon/>.</a>
      </div>
      <div id='contact-github'>
      <a href="https://github.com/AsifShaikh01" target="_blank" attributes-list><GitHubIcon/></a>
      </div>
      <div id="contact-linkedin">
      <a href="https://www.linkedin.com/in/asif-shaikh-74bb26146/" target="_blank" attributes-list><LinkedInIcon/></a>
      </div>

      </div>
    </div>
  )
}

export default Contact
