import React from 'react'
import "./Home.css";
import Pdf from '../resume.pdf';
import InsertDriveFileOutlinedIcon from '@mui/icons-material/InsertDriveFileOutlined';

import GetAppIcon from '@mui/icons-material/GetApp'


const Home = () => {
  return (
    <div className='home' id='home'>
        <div >
            <img className='home-img' src='https://avatars.githubusercontent.com/u/103635136?v=4' alt="Asif Shaikh" />
        </div>

        <div className='info' >
           <h5>Hey There!! I am</h5>
           <h1 id="user-detail-name">Asif Shaikh</h1>
           <h3>Full Stack Web Developer</h3>
           <a id="user-detail-intro">Aspiring Full Stack Web Developer with react and node JS (express) with 1200+ hours of coding experience looking forward to work with an organization where my better can become best.</a>
           <a id='resume-link-2' href ={Pdf} target="_blank" rel="noopener noreferrer" download="Asif_Shaikh_Resume.pdf"> 
           <button id='resume-button-2'><InsertDriveFileOutlinedIcon/>resume</button></a>

        </div>
        
    </div>
  )
}

export default Home;
